import {
  Component,
  Event,
  EventEmitter,
  Host,
  Listen,
  State,
  Watch,
  h,
} from '@stencil/core';
import { ApiCart } from '../../../../../utils/api/ecommerce/cart.api';
import { ApiProduct } from '../../../../../utils/api/ecommerce/product.api';
import {
  CartInfo,
  CartItem,
} from '../../../../../utils/api/ecommerce/utils/interfaces/cart.interface';
import { currency } from '../../../../../utils/filters/currency.filter';
import { CartItemData } from './utils/cart-list.interface';

@Component({
  tag: 'tp-elem-cart-list',
  styleUrl: 'cart-list.scss',
  scoped: true,
})
export class TpElemCartList {
  /**
   * Dados do carrinho.
   */
  @State() errorMessage: string;

  /**
   * Dados do carrinho.
   */
  @State() total: number;

  /**
   * Dados do carrinho.
   */
  @State() cartItems: CartItem[];

  /**
   * Dados do produto.
   */
  @State() itemsData: CartItemData[] = [];

  /**
   * Emite o updated cart.
   */
  @Event() updatedCart: EventEmitter<CartInfo>;

  componentWillLoad() {
    this.getCart();
  }

  async getCart() {
    const response = await ApiCart.getCart();
    this.cartItems = response.cart;
    this.total = response.cartTotal;
  }

  @Watch('cartItems')
  async watchCartItems() {
    const itemsDataPromises = [];

    this.cartItems.forEach((cartItem) => {
      const partialPromise = ApiProduct.getProductById(cartItem.id);
      itemsDataPromises.push(partialPromise);
    });

    const itemsData = await Promise.all(itemsDataPromises);

    this.itemsData = itemsData.map((product) => {
      const cartItem = this.cartItems.find(({ id }) => id === product.id);

      return {
        id: cartItem.id,
        quantity: cartItem.quantity,
        cartItemKey: cartItem.cartItemKey,
        name: product.name,
        shortDescription: product.shortDescription,
        images: product.images,
        regularPrice: product.regularPrice,
        salePrice: product.salePrice,
        stockQuantity: product.stockQuantity,
        permalink: product.permalink,
      };
    });
  }

  @Listen('tpChangeAlt', { target: 'window' })
  handlePlusminus(event: CustomEvent<number>) {
    const tpElemCartItem = (event.target as HTMLElement).closest(
      'tp-elem-cart-item',
    );
    const cartItemKey = tpElemCartItem.getAttribute('cart-key-item');
    const newQuantity = event.detail;

    this.setItemQuantity(cartItemKey, newQuantity);
  }

  // TODO - deletar, era só pra testar se emite o evento
  @Listen('updatedCart')
  handleUpdatedCartCount(event: CustomEvent<CartInfo>) {
    console.log(event.detail);
  }

  async setItemQuantity(cartItemKey: string, quantity: number) {
    try {
      const { success, cart, cartTotal, cartCount } = await ApiCart.updateCart(
        cartItemKey,
        quantity,
      );

      if (success) {
        this.cartItems = cart;
        this.total = cartTotal;
        this.errorMessage = '';

        const cartInfo: CartInfo = { cart, cartTotal, cartCount };
        this.updatedCart.emit(cartInfo);
      } else {
        this.errorMessage = 'O Carrinho não foi atualizado!';
      }
    } catch (error) {
      console.error(error);
    }
  }

  async onRemoveFromCart(id: number) {
    try {
      const { success, cart, cartTotal, cartCount } =
        await ApiCart.removeFromCart(id);

      if (success) {
        this.cartItems = cart;
        this.total = cartTotal;
        this.errorMessage = '';

        const cartInfo: CartInfo = { cart, cartTotal, cartCount };
        this.updatedCart.emit(cartInfo);
      } else {
        this.errorMessage = 'O Carrinho não foi atualizado!';
      }
    } catch (error) {
      console.log('Não foi possível remover o item do carrinho.');
    }
  }

  render() {
    const { itemsData, total, errorMessage } = this;

    const items = itemsData?.map((item) => {
      const {
        id,
        quantity,
        cartItemKey,
        name,
        shortDescription,
        images,
        regularPrice,
        salePrice,
        stockQuantity,
        permalink,
      } = item;

      const save = Number(regularPrice) - Number(salePrice);
      const itemTotal = salePrice
        ? Number(salePrice) * quantity
        : Number(regularPrice) * quantity;
      const totalSave = (Number(regularPrice) - Number(salePrice)) * quantity;

      return (
        <tp-elem-cart-item class="cart-list__item" cart-key-item={cartItemKey}>
          <a href={permalink} slot="image">
            <img src={images[0].src} alt={name} />
          </a>
          <ion-text tp-type="h20x" slot="title">
            <h2>{name}</h2>
          </ion-text>
          {salePrice && (
            <ion-text color="neutral-8" tp-type="p16x" slot="regular-price">
              <span>{currency(regularPrice, 'pt-br')}</span>
            </ion-text>
          )}
          <ion-text color="neutral-8" tp-type="p16xb" slot="price">
            <span>
              {currency(salePrice ? salePrice : regularPrice, 'pt-br')}
            </span>
          </ion-text>
          {salePrice && (
            <ion-text color="neutral-7" tp-type="p14x" slot="save">
              <span>Economize {currency(save, 'pt-br')}</span>
            </ion-text>
          )}
          <ion-text color="neutral-7" tp-type="p16x" slot="short-description">
            <p>{shortDescription.replace('<p>', '').replace('</p>', '')}</p>
          </ion-text>
          <tp-plusminus
            use-slot="false"
            value={Number(quantity)}
            min={1}
            max={stockQuantity ? stockQuantity : 9}
            automatic-disabled
            slot="plusminus"
          ></tp-plusminus>
          <ion-button
            color="brand"
            fill="clear"
            tp-no-padding
            slot="remove"
            onClick={() => this.onRemoveFromCart(id)}
          >
            Remover item
          </ion-button>
          <ion-text color="neutral-8" tp-type="h20x" slot="item-total">
            <span>{currency(itemTotal, 'pt-br')}</span>
          </ion-text>
          {salePrice && (
            <ion-text
              class="cart-item__save"
              color="neutral-7"
              tp-type="p14x"
              slot="save-total"
            >
              <span>Economize {currency(totalSave, 'pt-br')}</span>
            </ion-text>
          )}
        </tp-elem-cart-item>
      );
    });

    return (
      <Host>
        <div class="cart-list">
          {items}
          <div class="cart-list__total">
            {errorMessage && (
              <ion-text tp-type="p18xb" color="warning">
                <p>{errorMessage}</p>
              </ion-text>
            )}
            <ion-text tp-type="p18xb" color="neutral-8">
              <h4>Total:</h4>
            </ion-text>
            <ion-text tp-type="h24x">
              <h4>{currency(total, 'pt-br')}</h4>
            </ion-text>
          </div>
        </div>
      </Host>
    );
  }
}
